/* roboto-condensed-regular - cyrillic */
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "RobotoCondensed";

  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"),
    url("../fonts/roboto-condensed-regular.woff") format("woff");
}
